.header {
  border-bottom: 1px solid #002053;
}
.header__wrap {
  padding: 13px 0;
  display: flex;
  align-items: center;
  position: relative;
}
.header .logo {
  max-width: 111px;
  display: inline-block;
}
.header__menu {
  display: flex;
  align-items: center;
  margin: 5px 0 0 auto;
  padding: 0;
  list-style: none;
  font-size: 15px;
  text-transform: uppercase;
  font-weight: 700;
}
.header__menu-item {
  position: relative;
}
.header__menu-item:not(:last-of-type) {
  margin-right: 27px;
}
.header__menu-drop {
  position: absolute;
  top: 6px;
  right: -17px;
  width: 15px;
  height: 15px;
  background-image: url('../img/icons/arrow.png');
  background-size: 10px 7px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  transform: rotate(180deg);
  transition: 0.3s;
}
.header__menu-drop.active {
  transform: rotate(0);
}
.header__menu-sub {
  display: none;
  position: absolute;
  z-index: 2;
  width: 175px;
  top: 100%;
  left: -50%;
  background-color: #002053;
  color: #ffffff;
  margin: 0;
  padding: 20px 30px;
  list-style: none;
  font-size: 13px;
}
.header__phone {
  margin-left: auto;
  font-size: 15px;
  font-weight: 700;
}
.header .icons {
  display: flex;
  margin-left: 22px;
}
.header .icons .feedback {
  background-color: #47b69a;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .icons .search {
  background-color: #eceef1;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-left: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .burger {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  position: relative;
  background-color: #47b69a;
  display: none;
  margin-left: 22px;
}
.header .burger span {
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.header .burger span:nth-of-type(1) {
  bottom: 12px;
}
.header .burger span:nth-of-type(3) {
  top: 12px;
}
.header .mobile-menu {
  position: fixed;
  z-index: 2;
  right: 0;
  top: 0;
  width: 70%;
  height: 100vh;
  max-height: 100vh;
  background-color: #002053;
  display: none;
  overflow: auto;
}
.header .mobile-menu__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  padding: 80px 40px 40px;
}
.header .mobile-menu__close {
  position: absolute;
  top: 40px;
  right: 40px;
  background-color: #47b69a;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .search-block {
  display: none;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  padding: 40px;
}
.header_mainpage {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
  border: none;
}
